<template>
  <div class="layout">
    <Sider
      :style="{position: 'fixed', height: '100vh', left: 0, overflow: 'auto', width: '150px',minWidth: '150px'}"
    >
      <CellGroup>
        <Cell
          @click.native="commodityMenuItemClick"
          title="商品"
          :style="{padding: '14px 24px',color: 'rgba(255,255,255,.7)'}"
        />
        <Cell
          @click.native="customerMenuItemClick"
          title="客户"
          :style="{padding: '14px 24px',color: 'rgba(255,255,255,.7)'}"
        />
        <Cell
          @click.native="historyMenuItemClick"
          title="历史销售单"
          :style="{padding: '14px 24px',color: 'rgba(255,255,255,.7)'}"
        />
        <Cell
          @click.native="backMenuItemClick"
          title="退货单"
          :style="{padding: '14px 24px',color: 'rgba(255,255,255,.7)'}"
        />
        <Cell
          @click.native="backHistoryMenuItemClick"
          title="历史退货单"
          :style="{padding: '14px 24px',color: 'rgba(255,255,255,.7)'}"
        />
      </CellGroup>
    </Sider>
    <Layout :style="{paddingLeft:'150px'}">
      <Content>
        <Tabs
          type="card"
          :value="currentTabPaneId"
          @on-tab-remove="handleTabRemove"
          @on-click="handleTabClick"
          :style="{margin:'20px'}"
        >
          <TabPane
            :closable="tabPane.id!==0"
            v-for="tabPane in tabPaneList"
            :key="tabPane.id"
            :name="tabPane.id+''"
            :label="tabPane.label"
          >
            <component :is="tabPane.component" />
          </TabPane>
        </Tabs>
      </Content>
    </Layout>
  </div>
</template>
<script>
import { EventBus } from "../common/EventBus";
export default {
  data() {
    return {
      name: "",
      currentTabPaneId: "0",
      tabPaneList: []
    };
  },
  created() {
    if (sessionStorage.getItem("user") == null) {
      this.$router.push({ name: "login" });
      return;
    }
    this.tabPaneList.push({
      id: 0,
      label: "销售单",
      component: () => import("../components/Sale.vue")
    });
  },
  components: {},
  computed: {},
  methods: {
    // 商品菜单条目点击
    commodityMenuItemClick() {
      if (this.isAddTabPane(1)) {
        this.currentTabPaneId = 1 + "";
        return;
      }
      this.tabPaneList.push({
        id: 1,
        label: "商品",
        component: () => import("../components/Commodity.vue")
      });
      this.currentTabPaneId = 1 + "";
    },
    // 客户菜单条目点击
    customerMenuItemClick() {
      if (this.isAddTabPane(2)) {
        this.currentTabPaneId = 2 + "";
        return;
      }
      this.tabPaneList.push({
        id: 2,
        label: "客户",
        component: () => import("../components/Customer.vue")
      });
      this.currentTabPaneId = 2 + "";
    },
    // 历史销售单菜单条目点击
    historyMenuItemClick() {
      if (this.isAddTabPane(3)) {
        this.currentTabPaneId = 3 + "";
        return;
      }
      this.tabPaneList.push({
        id: 3,
        label: "历史销售单",
        component: () => import("../components/History.vue")
      });
      this.currentTabPaneId = 3 + "";
    },
     // 退货单菜单条目点击
    backMenuItemClick() {
      if (this.isAddTabPane(4)) {
        this.currentTabPaneId = 4 + "";
        return;
      }
      this.tabPaneList.push({
        id: 4,
        label: "退货单",
        component: () => import("../components/BackSale.vue")
      });
      this.currentTabPaneId = 4 + "";
    },
     // 历史退货单菜单条目点击
    backHistoryMenuItemClick() {
      if (this.isAddTabPane(5)) {
        this.currentTabPaneId = 5 + "";
        return;
      }
      this.tabPaneList.push({
        id: 5,
        label: "历史退货单",
        component: () => import("../components/BackHistory.vue")
      });
      this.currentTabPaneId = 5 + "";
    },
    // tab item 删除
    handleTabRemove(name) {
      for (let i = 0; i < this.tabPaneList.length; i++) {
        if (this.tabPaneList[i].id === parseInt(name)) {
          this.tabPaneList.splice(i, 1);
          this.currentTabPaneId = 0 + "";
          return;
        }
      }
    },
    // tab item 点击监听
    handleTabClick(name) {
      this.currentTabPaneId = name;
    },
    // 判断菜单条目是否添加到了tab中
    isAddTabPane(id) {
      for (let i = 0; i < this.tabPaneList.length; i++) {
        if (this.tabPaneList[i].id === id) {
          this.currentTabPaneId = id + "";
          return true;
        }
      }
    }
  },
  mounted() {
    let _this = this;
    EventBus.$on("catOrder", ({ code }) => {
      _this.historyMenuItemClick();
    }),
     EventBus.$on("catBackOrder", ({ code }) => {
      _this.backHistoryMenuItemClick();
    })
  }
};
</script>

<style scoped>
.ivu-cell:hover {
  background: #5b6270;
}
</style>
